import React from 'react'
import {Card,Button,Radio } from 'antd'
import { SearchOutlined,PlusCircleOutlined,EditOutlined,DeleteOutlined,DownloadOutlined,CloseOutlined
,LeftOutlined,RightOutlined } from '@ant-design/icons';
import './ui.less'

export default class Buttons extends React.Component{
    

    state = {
        loading : true,
        size:'default'
    }
    //loading
    handleCloseLoading = ()=>{
        this.setState({
            loading:false
        });
    }
    //
    handleChange = (e) =>{
        this.setState({
            size: e.target.value
        });
    }

    render(){
        // const {size} = this.state;
        return(
            <div>
                <Card title='' className='card-warp'>
                    <Button type='primary'>Imooc</Button>
                    <Button>Imooc</Button>
                    <Button type='dashed'>Imooc</Button>
                    <Button disabled>Imooc</Button>
                    <Button type='link'>Imooc</Button>
                </Card>
                <Card title='' className='card-warp'>
                    <Button icon={<PlusCircleOutlined />}></Button>
                    <Button icon={<EditOutlined />}></Button>
                    <Button icon={<DeleteOutlined />}></Button>
                    <Button shape='circle' icon={<SearchOutlined />}></Button>
                    <Button icon={<SearchOutlined />} type='primary'></Button>
                    <Button type='primary' icon={<DownloadOutlined />}></Button>
                </Card>
                <Card title='Loading' className='card-warp'>
                    <Button type='primary' loading={this.state.loading} onClick='this.handleClickLoading'></Button>
                    <Button type='primary' shape='circle' loading></Button>
                    <Button loading={this.state.loading}></Button>
                    <Button shape='circle' loading></Button>
                    <Button type='primary' onClick={this.handleCloseLoading} shape='circle' icon={<CloseOutlined />}></Button>
                </Card>
                <Card title='' className='card-warp'>
                    <Button.Group>
                        <Button icon={<LeftOutlined />} style={{marginRight:0}}></Button>
                        <Button icon={<RightOutlined />}></Button>
                    </Button.Group>
                </Card>
                <Card title='' className='card-warp'>
                    <Radio.Group value={this.state.size} onChange={this.handleChange.bind(this)}>
                        <Radio value="small"></Radio>
                        <Radio value="default"></Radio>
                        <Radio value="large"></Radio>
                    </Radio.Group>
                    <Button type='primary' size={this.state.size}>Imooc</Button>
                    <Button size={this.state.size}>Imooc</Button>
                    <Button type='dashed' size={this.state.size}>Imooc</Button>
                    <Button disabled size={this.state.size}>Imooc</Button>   

                </Card>
            </div>
        );
    }
}