import React from 'react'
import {Card,Button,Table,message,Modal,Form, Input,Select,Tree,Transfer} from 'antd'
import Utils from './../../utils/utils'
import axios from './../../axios/index'
import menuConfiger from './../../config/menuConfig'

const FormItem = Form.Item;
const Option = Select.Option;
const TreeNode = Tree.TreeNode;

export default class Permission extends React.Component{
    userForm = React.createRef();   //ref

    state={
        // usetList:[]
        isVisable:false,
        isPromission:false,
        isUserVisable:false,
        detailInfo:{}
    }
    params={
        page:1
    }

    componentDidMount(){
        this.requsetList();
    }

    //
    requsetList=()=>{
        axios.ajax({
            url:'/table/role/list',     //
            data:this.params.page       
            
        }).then((res)=>{
            if(res.code == '0'){        //
                this.setState({
                    //usetListres
                    usetList:res.result.item_list.map((item,index)=>{
                        item.key=index;     //key
                        return item;
                    })
                })
            }
        })
        // console.log(this.state.usetList)
    }

    //
    handleCreate=()=>{
        this.setState({
            isVisable:true      //
        })
    }
    //
    handleSubmit=()=>{
        let userInfo = this.userForm.current.getFieldsValue();
        console.log(userInfo)
        message.success(`${userInfo.role_name} `)
        this.setState({isVisable:false})
        this.userForm.current.resetFields();   //
    }
    //
    handlePermission=()=>{
        let item = this.state.selectItem;   //
        if(!item){
            message.error('')
            return;
        }else{
            this.setState({
                //
                isPromission:true,
                detailInfo:item      //detailInfo 
            })
            // console.log(item)
        }
        
    }
    //
    handlePreSubmit=()=>{
        this.setState({
            isPromission:false
        })
        message.success('')
    }
    //modalNodetree
    renderTreeNodes=(data)=>{
        return data.map((item)=>{           //
            if(item.children){              //item
                return <TreeNode title={item.title} key={item.key} >
                    {this.renderTreeNodes(item.children)}        {/**/}
                </TreeNode>
            }else{
                return <TreeNode title={item.title} key={item.key}></TreeNode>
            }
        })
    }

    //
    onCheck=(checkedKeys)=>{
        
    } 
    //
    handleUserAuth=()=>{
        let item  = this.state.selectItem;
        if(!item){
            message.error('')
        }else{
            this.getRoleUserList(item.id);
        }
    } 
    //
    getRoleUserList=(id)=>{
        let item = this.state.selectItem    //item
        axios.ajax({
            url:'/table/role/user_list',
            data:{
                params:{id}
            }
        }).then((res)=>{
            if(res.code == '0'){
                this.setState({
                    isUserVisable:true,      //
                    dataAuthInfo:item
                })
                this.getAuthUserList(res.result);   //
                console.log(res)
            }
        })
    }
    //
    getAuthUserList = (dataSource)=>{
        const mockData = [];        //dataSource
        const targetKeys = [];      //targetkeys
        if(dataSource && dataSource.length>0){
            for(let i=0;i<dataSource.length;i++){
                const data={
                    key:dataSource[i].user_id,              //idkey
                    title:dataSource[i].user_name,          
                    status:dataSource[i].status
                }            
                if(data.status == "1"){
                targetKeys.push(data.key);           //targetkeyskey
                }
                mockData.push(data);         //dataSource
            }
            this.setState({
                mockData,           //
                targetKeys
            })
        }
    }

    //
    handleUserSubmit=()=>{
        let data = {};
        data.user_id = this.state.targetKeys;
        data.role_id = this.state.selectItem.id;
        axios.ajax({
            url:'/table/role/userlist_success',
            data:data
        }).then((res)=>{
            if(res.code == 0){
                message.success('')
                this.setState({
                    isUserVisable:false
                })
            }
        })
    }

    render(){
        
        const columns=[
            {
                title:'ID',
                dataIndex:'id'
            },
            {
                title:'',
                dataIndex:'role_name'
            },
            {
                title:'',
                dataIndex:'create_time',
                render:Utils.formateDate
            },
            {
                title:'',
                dataIndex:'status',
                render(status){
                    return status == 1? "":""
                }
            },
            {
                title:'',
                dataIndex:'authorize_time',
                render:Utils.formateDate
            },
            {
                title:'',
                dataIndex:'authorize_user_name'
            }
        ]
        //
        const rowSelection = {
            type:'radio',
            selectedRowKeys:this.state.selectedRowKeys,
        }
        //
        const formLayout = {
            labelCol:{span:5},
            wrapperCol:{span:10}
        }
        return(
            <div>
                <Card title='' className='card-warp'>
                    <Button onClick={this.handleCreate}></Button>
                    <Button onClick={this.handlePermission}></Button>
                    <Button type='primary' onClick={this.handleUserAuth}></Button>

                    <Table
                        bordered
                        rowSelection={rowSelection}
                        style={{marginTop:15}}
                        columns={columns}
                        dataSource={this.state.usetList}
                        onRow={(record,index)=>{
                            return{
                                onClick:()=>{
                                    this.setState({
                                        selectedRowKeys:[index],
                                        selectItem:record
                                    })
                                    message.info(`${record.authorize_user_name} ${record.status}`)
                                }
                            }
                        }}
                    ></Table>
                </Card>
                 {/* */}
                <Modal visible={this.state.isVisable}
                onCancel={()=>{this.setState({isVisable:false})}}
                title=''
                onOk={this.handleSubmit}
                >
                    <Form ref={this.userForm}>
                        <FormItem label=''
                        name='role_name'
                        {...formLayout}
                        ><Input placeholder=''></Input></FormItem>
                        <FormItem label=''
                        name='role_status'
                        {...formLayout}
                        >
                            <Select>
                                <Option value='1'></Option>
                                <Option value='0'></Option>
                            </Select>
                        </FormItem>
                    </Form>
                </Modal>
                 {/*         */}
                <Modal title='' visible={this.state.isPromission}
                onCancel={()=>{this.setState({isPromission:false})}}
                onOk={this.handlePreSubmit}
                >
                    <Form>
                        <FormItem label='' {...formLayout}><Input disabled placeholder={this.state.detailInfo.role_name}/></FormItem>
                        <FormItem label='' {...formLayout} >
                            <Select defaultValue={1}>
                                <Option value={1}></Option>
                                <Option value={0}></Option>
                            </Select>
                        </FormItem>
                        <Tree
                        checkable
                        defaultExpandAll
                        onCheck={(checkedKeys)=>{
                            this.onCheck(checkedKeys)
                        }}
                        checkedKeys={this.state.detailInfo.menus}
                        >
                            <TreeNode title=''>
                                {this.renderTreeNodes(menuConfiger)}
                            </TreeNode>
                        </Tree>
                    </Form>
                </Modal>
                 {/*        */}
                 <Modal
                 title=''
                 visible={this.state.isUserVisable}
                 onCancel={()=>{this.setState({isUserVisable:false})}}
                 onOk={this.handleUserSubmit}
                 >
                <RoleAuthForm ref={this.AuthForm}
                mockData={this.state.mockData}
                targetKeys={this.state.targetKeys}
                dataAuthInfo = {this.state.dataAuthInfo}
                patchUserInfo = {(targetKeys)=>{
                    this.setState({targetKeys})
                }}
                ></RoleAuthForm>     
                </Modal>       
            </div>
        );
    }
}

class RoleAuthForm extends React.Component{

    //
    filterOption=(inputValue, option)=>{
        return option.title.indexOf(inputValue) > -1;
    }
    //
    handleChange=(targetKeys)=>{
        this.props.patchUserInfo(targetKeys);
    }
    render(){
        const dataAuthInfo = this.props.dataAuthInfo;
        return(
            
                <Form>
                    <FormItem
                    label=''
                    name='rolename'
                    disabled
                    style={{width:400}}
                    ><Input disabled placeholder={dataAuthInfo.role_name}></Input></FormItem>
                    <FormItem>
                    <Transfer
                        dataSource={this.props.mockData}
                        titles={['', '']}
                        showSearch
                        searchPlaceholder=''
                        targetKeys={this.props.targetKeys}
                        filterOption={this.filterOption}
                        style={{ marginBottom: 16 }}
                        render={item => item.title}
                        onChange={this.handleChange}
                        />

                    </FormItem>
                </Form>
            
        );
    }
}