import React from 'react'
import {Card} from 'antd'
import echarts from 'echarts/lib/echarts'
import ReactEcharts from 'echarts-for-react'
import echartTheme from './../echartTheme'


import 'echarts/lib/chart/line'          //
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/markPoint'

export default class Line extends React.Component{

    componentWillMount(){
        echarts.registerTheme('IMOOC',echartTheme); //
    }

    getOption=()=>{
        let option={
            title:{
                text:''
            },
            //
            tooltip:{
                trigger:'axis'
            },
            //x
            xAxis:{
                data:['','','','','','','']
            },
            //y,y
            yAxis:{
                type:'value'
            },
            series:[
                {
                    name:'',
                    type:'line',
                    data:[1000,2000,1520,1200,3522,1400,5200]
                }
            ]

        }
        return option;
    }
    getOption2=()=>{
        let option={
            title:{
                text:''
            },
            //
            tooltip:{
                trigger:'axis'
            },
            //x
            xAxis:{
                data:['','','','','','','']
            },
            //y,y
            yAxis:{
                type:'value'
            },
            series:[
                {
                    name:'',
                    type:'line',
                    data:[1000,2000,1520,1200,3522,1400,5200]
                },
                {
                    name:'',
                    type:'line',
                    data:[2000,3000,5000,2600,1300,4100,6500]
                },
            ],
            legend:{
                data:['','']
            }
        }
        return option;
    }
    render(){
        return(
            <div>
                <Card title='' className='card-warp'>
                    <ReactEcharts option={this.getOption()} theme='IMOOC'></ReactEcharts>
                </Card>

                <Card title='2' className='card-warp'>
                    <ReactEcharts option={this.getOption2()} theme='IMOOC'></ReactEcharts>
                </Card>
            </div>
        );
    }
}