import React from 'react'
import {Button, Card,message,Table} from 'antd'
import axios from './../../axios/index'
import Modal from 'antd/lib/modal/Modal'
import utils from './../../utils/utils'

export default class BasicForm extends React.Component{
    state={
        dataSource2:[]
    }

    componentDidMount(){
        const dataSource =[
            {
                id:'0',
                userName:'Jack',
                sex:'1',
                state:'1',
                interest:'1',
                birthday:'2020-10-05',
                address:'',
                time:'08:00'
            },
            {
                id:'1',
                userName:'Marry',
                sex:'1',
                state:'1',
                interest:'1',
                birthday:'2020-10-06',
                address:'',
                time:'08:00'
            },
            {
                id:'2',
                userName:'Tom',
                sex:'1',
                state:'1',
                interest:'1',
                birthday:'2020-10-07',
                address:'',
                time:'08:00'
            },
        ]
        dataSource.map((item,index)=>{
            item.key = index;
        })
        this.setState({ dataSource:dataSource})
        this.request();
    }
    //
    request =()=>{
        
        axios.ajax({
            url:'/table/table',
            data:{
                params:{
                    page:1
                }
            }
        }).then((res)=>{
            if(res.code == '0'){
                res.result.list.map((item,index)=>{
                    item.key = index;
                })
                this.setState({
                    
                    dataSource2:res.result.list,
                    pagination:utils.pagination(res,(current)=>{
                        //to do
                    })
                })
            }
        })
    }
   
    //
    handleDelete=(()=>{
        let rows= this.state.selectedRows; 
        let ids = [];
        rows.map((item)=>{
            ids.push(item.id)
        })
        Modal.confirm({
            title:'',
            content:`${ids.join(',')}`,
            onOk:()=>{message.success('')}
        })
        this.setState({
            selectedRowKeys:[],
            selectedRows:null
        })
    })
    render(){
        const columns = [
            {
                title:'id',
                dataIndex:'id',
                sorter:(a,b)=> a.id-b.id,
            },
            {
                title:'',
                dataIndex:'userName',
                sorter:(a,b)=>a.userName.length-b.userName.length
            },
            {
                title:'',
                dataIndex:'sex',
                render(sex){
                    return(
                        sex == 1?'':''
                    )
                }
            },
            {
                title:'',
                dataIndex:'state',
                render(state){
                    let config = {
                        '1':"",
                        '2':"",
                        '3':"",
                        '4':"FE",
                        '5':"IT"
                    }
                    return config[state];
                }
            },
            {
                title:'',
                dataIndex:'interest',
                render(interest){
                    let config = {
                        '1':"",
                        '2':"",
                        '3':"",
                    }
                    return config[interest]
                }
            },
            {
                title:'',
                dataIndex:'birthday',
            },
            {
                title:'',
                dataIndex:'address',
            },
            {
                title:'',
                dataIndex:'time',
            }
        ]
        const {selectedRowKeys} = this.state;
        //rowSelection,
        const rowSelection={
            type:"radio",
            selectedRowKeys
        }
        //
        const rowCheckSelection = {
            type:'checkbox',
            selectedRowKeys,
            onChange:(selectedRowKeys,selectedRows)=>{
                this.setState({
                    selectedRowKeys,
                    selectedRows
                    
                })
            }
        }
        return(
            <div>
                <Card title='' className='card-warp'>
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.dataSource}
                        pageination={false}
                    ></Table>
                </Card>
                <Card title='' className='card-warp'>
                    <Table
                    bordered
                    columns={columns}
                    dataSource={this.state.dataSource2}
                    pageination={false}
                    >
                    </Table>
                </Card>
                <Card title='MOCK-' className='card-warp'>
                    <Table
                    rowSelection={rowSelection}         //or
                    bordered
                    columns={columns}
                    dataSource={this.state.dataSource2}
                    pageination={false}
                    // onRow={(record,index) => {          //key
                        // return {
                        //   onClick: () => {
                        //       let selectkey = [index];      //selectkeykey
                        //       Modal.info({
                        //           title:'UserInfo',
                        //           content:`${record.userName} ${record.interest}`
                        //       })
                        //       this.setState({
                        //           selectedRowKeys:selectkey,    //selectedRowKeyskey
                        //           selectedItem : record
                        //       })
                        //   }, // 
                        // };
                    //   }}
                    >

                    </Table>
                </Card>

                <Card title='MOCI-' className='card-warp'>
                    <div><Button onClick={this.handleDelete} style={{margin:15}}></Button></div>
                      <Table
                      columns={columns}
                      dataSource={this.state.dataSource2}
                      rowSelection={rowCheckSelection}
                      pageination={false}
                      >

                      </Table>
                </Card>
                <Card title='MOCI-' className='card-warp'>

                      <Table
                      columns={columns}
                      dataSource={this.state.dataSource2}
                    //   rowSelection={rowCheckSelection}
                      pagination={this.state.pagination}
                      >

                      </Table>
                </Card>
            </div>
        );
    }
}