import React from 'react'
import { Card,DatePicker,Form, Input,Select,message, Button, Table,Modal, Radio } from 'antd'
import ButtonGroup from 'antd/lib/button/button-group';
import {PlusOutlined,EditOutlined,DeleteOutlined,ExclamationCircleOutlined } from '@ant-design/icons';
import axios from './../../axios/index'
import utils from './../../utils/utils'
import TextArea from 'antd/lib/input/TextArea';
import moment from 'moment'

const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

export default class User extends React.Component{
    constructor(props){
        super(props)
        this.state={}
        // this.userForm = React.createRef();
    }
    
    
    
    render(){
        return(
            <div>
                <QueryUser/>
                <UserForm />
            </div>
        );
    }
}

//
class QueryUser extends React.Component{

    //
    formSubmit=(values)=>{
        if(values.username == undefined){message.error('')}
        else{
            message.info(`${values.username} ${values.mobile} ${values.datein}`)
        }
    }
    //
    handleClear=(e)=>{
        this.refs.myFrom.resetFields();
    }

    render(){
        return(
            <Card className='card-warp'>
                <Form layout='inline' onFinish={this.formSubmit} ref='myFrom'>
                    <FormItem
                    label=''
                    name='username'
                    >
                        <Input placeholder=''></Input>
                    </FormItem>
                    <FormItem
                    label=''
                    name='mobile'
                    >
                        <Input placeholder=''></Input>
                    </FormItem>
                    <FormItem
                    label=''
                    name='datein'
                    >
                        <DatePicker></DatePicker>
                    </FormItem>
                    <FormItem>
                        <Button type='primary' htmlType='submit' style={{marginLeft:15}}></Button>
                        <Button onClick={this.handleClear}></Button>
                    </FormItem>
                </Form>
            </Card>
        );
    }
}

//user
class UserForm extends React.Component{
    
    userForm = React.createRef();

    state={
        userInfo:{}
    }
    
    params={ page:1}

    //userlist
    requsetUserlist=()=>{
        let _this = this;
        axios.ajax({
            url:'/table/userlist',
            data:this.params.page
        }).then((res)=>{
           this.setState({
               userlist:res.result.list.map((item,index)=>{
                   item.key = index;
                   return item;
               }),
               pagination:utils.pagination(res,(current)=>{
                _this.params.page = current;
                _this.requsetUserlist();
            })
           })
        })
    }

    componentDidMount(){
        this.requsetUserlist();
    }

    
    handleOperate=(type)=>{
        let item = this.state.selectedItem;
        console.log(type)
        if(type == 'create'){
            this.setState({
                type,
                isVisable:true,
                title:''
            })
        }else if(type == 'edit'){
            
            if(!item){
                message.error('')
                return;
            }else{
            this.setState({
                type,
                isVisable:true,
                title:'',
                userInfo:item
            })
           console.log(item)
            }
        }else if(type=='detail'){
            if(!item){
                message.error('')
                return;
            }else{
            this.setState({
                type,
                isVisable2:true,
                title:'',
                userInfo:item
            })
            }
        }else{if(!item){
            message.error('')
            return;
        }else{
            let _this = this;
            Modal.confirm({
                title:'',
                content:'',
                onOk(){
                    message.success('')
                    _this.setState({
                        isVisable:false
                    })
                    _this.requsetUserlist();
                }
            })
        }
        }
        
    }
    // handleShowModal=()=>{
    //     this.setState({
    //         isVisable:true
    //     })
    // }
    //submit
    handleSubmit=()=>{
        let type = this.state.type;
        let data = this.userForm.current.getFieldsValue();
        // console.log(type);
        // console.log(data);
        if(type == 'create'){
            axios.ajax({
                url:'/table/add',
                data:{
                    params:data
                }
            }).then((res)=>{
                if(res.code == 0){
                    this.setState({
                        isVisable:false ,
                    })
                }})
            message.success('')
            this.userForm.current.resetFields();
            // this.requsetUserlist();
        }else if(type == 'edit'){
            this.userForm.current.resetFields();
            message.success('')
            this.setState({
                isVisable:false
            })
        }else if(type== 'delete'){
            message.info('')
        }else{
            this.userForm.current.resetFields();
        }    
    }
    //
    getState=(state)=>{
        return{
            "1":"",
            "2":"",
            "3":""
        }[state]
    }
    render(){
        let userInfo = this.state.userInfo || {};
        const formItemLayout = {
            labelCol : {span:5},
            wrapperCol:{span:15}
        }
        const usercoumns =[
            {
                title:'id',
                dataIndex:'id'
            },
            {
                title:'',
                dataIndex:'username'
            },
            {
                title:'',
                dataIndex:'sex',
                render(sex){
                    return(
                        sex == 1?"":""
                    )   
                }
            },
            {
                title:'',
                dataIndex:'state',
                render(state){
                    let config = {
                        "1":"",
                        "2":"",
                        "3":""
                    }
                    return config[state];
                }
            },
            {
                title:'',
                dataIndex:'interest',
                render(interest){
                    let config = {
                        "1":"",
                        "2":"",
                        "3":""
                    }
                    return config[interest];
                }
            },
            {
                title:'',
                dataIndex:'birthday'
            },
            {
                title:'',
                dataIndex:'address'
            },
            {
                title:'',
                dataIndex:'time'
            }
        ]
        const rowSelection ={
            type:"radio",
            selectedRowKeys:this.state.selectedRowKeys
        }

        return(
            <div>
                <Card className='card-warp'>
                    <ButtonGroup style={{marginBottom:10}}>
                    <Button type='default' onClick={()=>this.handleOperate('create')}><PlusOutlined /></Button>
                    <Button type='default' onClick={()=>this.handleOperate('detail')}><ExclamationCircleOutlined /></Button>
                    <Button type='default' onClick={()=>this.handleOperate('edit')}><EditOutlined /></Button>
                    <Button type='danger' onClick={()=>this.handleOperate('delete')}><DeleteOutlined /></Button>
                    </ButtonGroup>

                    <Table
                    bordered 
                    selectedItem = {this.state.selectedItem}
                    rowSelection={rowSelection}
                    pagination={this.state.pagination}
                    columns={usercoumns}
                    dataSource={this.state.userlist}
                    onRow={(record,index)=>{
                        return{
                            onClick:()=>{
                                this.setState({
                                    selectedRowKeys:[index],
                                    selectedItem:record
                                })
                            
                            }
                        }
                    }}
                    />
                    
                </Card>
                
                    <div>
                        <Modal 
                            title={this.state.title}
                            visible={this.state.isVisable}
                            onOk={this.handleSubmit}
                            onCancel={()=>{
                                this.setState({
                                    isVisable:false 
                                })
                            }}
                            >
                                <Form type={this.state.type}  ref={this.userForm} userInfo={this.state.userInfo}> 
                                    <FormItem name='user_name' label="" {...formItemLayout} initialValue={userInfo.username}>
                                        <Input type='text' placeholder=''/>
                                    </FormItem>
                                    <FormItem name='sex' label="" {...formItemLayout} initialValue={userInfo.sex}>
                                        <RadioGroup>
                                            <Radio value={1}></Radio>
                                            <Radio value={2}></Radio>
                                        </RadioGroup>
                                    </FormItem>
                                    <FormItem name='state' label="" {...formItemLayout} initialValue={userInfo.state}>
                                        <Select>
                                            <Option value={1}></Option>
                                            <Option value={2}></Option>
                                            <Option value={3}></Option>
                                        </Select>
                                    </FormItem>
                                    <FormItem name='birthday' label=''  {...formItemLayout} initialValue={moment(userInfo.birthday )}>
                                        <DatePicker></DatePicker>
                                    </FormItem>
                                    <FormItem name='address' label=''  {...formItemLayout} initialValue={userInfo.address}>
                                        <TextArea rows={3} placeholder=''></TextArea>
                                    </FormItem>
                                </Form>
                        </Modal>
                    </div>

                    <div>
                        <Modal 
                            title={this.state.title}
                            visible={this.state.isVisable2}
                            onOk={this.handleSubmit}
                            onCancel={()=>{
                                this.setState({
                                    isVisable2:false 
                                })
                            }}
                            footer={null}
                            >
                                <Form type={this.state.type}  ref={this.userForm} userInfo={this.state.userInfo}> 
                                    <FormItem name='user_name' label="" {...formItemLayout} initialValue={userInfo.username}>
                                    {userInfo.username}
                                    </FormItem>
                                    <FormItem name='sex' label="" {...formItemLayout} initialValue={userInfo.sex}>
                                        {userInfo.sex == 1?'':''}
                                       
                                    </FormItem>
                                    <FormItem name='state' label="" {...formItemLayout} initialValue={userInfo.state}>
                                        {this.getState(userInfo.state)}
                                    </FormItem>
                                    <FormItem name='birthday' label=''  {...formItemLayout} initialValue={moment(userInfo.birthday )}>
                                    {userInfo.birthday }
                                    </FormItem>
                                    <FormItem name='address' label=''  {...formItemLayout} initialValue={userInfo.address}>
                                    {userInfo.address}
                                    </FormItem>
                                </Form>
                        </Modal>
                    </div>
            </div>
        );
    }
}

