import React from 'react'
import {Button, Card,Modal} from 'antd'
import { Editor } from 'react-draft-wysiwyg';   //editor
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';      //css
import draftToHtml from 'draftjs-to-html' //html

export default class Rich extends React.Component{
    state={}

    //
    onEditorStateChange=(editorState)=>{
        this.setState({
            editorState,
            showRichText:false
        })
    }
     //,
     onEditorChange=(editorContent)=>{
        this.setState({
            editorContent
        });
    }

    //
    handleClearContent=()=>{
        this.setState({
            editorState: ''
        })
    }
    
    //html
    handleGetHtml=()=>{
        this.setState({
            showRichText:true
        })
    }

    render(){
        const {editorState} = this.state;
        return(
            <div>
                <Card>
                    <Button type='danger' onClick={this.handleClearContent}></Button>
                    <Button onClick={this.handleGetHtml}>HTML</Button>
                </Card>
                <Card title='' >
                    <Editor
                        // style={{height:500}}
                        editorState={editorState}
                        onContentStateChange={this.onEditorChange}    //
                        onEditorStateChange={this.onEditorStateChange}      //
                        />
                </Card>
                <Modal
                title='html'
                visible={this.state.showRichText}
                onCancel={()=>{
                    this.setState({showRichText:false})
                }}
                footer={null}
                >
                    {draftToHtml(this.state.editorContent)}
                </Modal>
            </div>
        );
    }
}