import React from 'react';
import {Card,Button,Form, Select, Table, Radio,message} from 'antd'
import axios from './../../axios/index'
import utils from './../../utils/utils'
// import { render } from 'less';
import Modal from 'antd/lib/modal/Modal';
// import { render } from 'less';

const FormItem = Form.Item;
const Option = Select.Option;

export default class City extends React.Component{

    
    render(){
        return(
            <div>
                <FilterForm></FilterForm>
                <CityForm></CityForm>
            </div>
        )
    }
}

//
class FilterForm extends React.Component{
    render(){
        
        return(
            <Form layout='inline' style={{marginBottom:20,marginLeft:20}}>
                <FormItem label="" name='city_id'>
                            <Select placeholder=''>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                                <Option value='3'></Option>
                            </Select>
                </FormItem>
                <FormItem label="" name='mode'>
                            <Select placeholder=''>
                                <Option value=''></Option>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                </FormItem>
                <FormItem label="" name='op_mode'>
                            <Select placeholder=''>
                                <Option value=''></Option>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                </FormItem>
                <FormItem label="" name='auth_status'>
                            <Select placeholder=''>
                                <Option value=''></Option>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                </FormItem>
                <FormItem>
                    <Button type='primary' style={{marginLeft:100}}></Button>
                    <Button></Button>
                </FormItem>
            </Form>
        );
    }
}


//+
class CityForm extends React.Component{
    state = {
        list : [],
        isShowOpenCity:false,
    }
    //Params
    params = {
        page:1
    }
    formRef = React.createRef();

    //
    handSubmit = (e) => {
        // let userInfo = this.formRef.current.getFieldsValue(values);
        // console.log(userInfo);
        message.info(e+"")
        this.setState({
            isShowOpenCity:false
        })

    };
    //
    componentDidMount(){
        this.requestList(); 
    }

    //axios
    requestList(){
        let _this = this;
        axios.ajax({
            url:'/table/open_city',
            data:{
                params:{
                    page:this.params.page
                }
            }
        }).then((res)=>{
            this.setState({
                list:res.result.item_list.map((item,index)=>{
                    item.key = index;
                    return item;
                }) , 
                pagination:utils.pagination(res,(current)=>{
                    _this.params.page = current;
                    _this.requestList();
                })
            })
        })
    }
    
    //
    handleOpenCity = ()=>{
        this.setState({
            isShowOpenCity:true
        })
    }
    // changeData(e){
    //     console.log(e)
        
    // }
    render(){
        const formItemLayout ={
            labelCol:{
                span:6,
            },
            wrapperCol:{
                span:12,
            }
        }
        const columns = [
            {
                title:'ID',
                dataIndex:'id'
            },
            {
                title:'',
                dataIndex:'name'
            },
            {
                title:'',
                dataIndex:'mode',
                render(mode){
                    return mode == 1?'':''
                }
            },
            {
                title:'',
                dataIndex:'op_mode',
                render(op_mode){
                    return op_mode == 1?'':''
                }
            },
            {
                title:'',
                dataIndex:'franchisee_name'
            },
            {
                title:'',
                dataIndex:'city_admins',
                render(arr){
                    return arr.map((item)=>{
                        return item.user_name
                    }).join(',');
                }
            },
            {
                title:'',
                dataIndex:'open_time'
            },
            {
                title:'',
                dataIndex:'update_time',
                render:utils.formateDate
            },
            {
                title:'',
                dataIndex:'sys_user_name'
            },
        ]
        return(
            <div>
                <Card className='card-warp'>
                    <Button type='primary' onClick={this.handleOpenCity} style={{marginBottom:10}}></Button>
                    <Table
                    bordered
                    columns={columns}
                    dataSource={this.state.list}
                    pagination={this.state.pagination}
                    ></Table>
                </Card>
                <Modal
                    title=''
                    visible={this.state.isShowOpenCity}
                    onCancel={()=>{
                        this.setState({
                            isShowOpenCity:false
                        })
                    }}
                    onOk={this.handSubmit}
                >
                    <Form ref={this.formRef}>
                        <FormItem {...formItemLayout} label='' >
                            <Select onChange={(e)=>{this.handSubmit(e)}}>
                                <Option value=''></Option>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                        </FormItem>
                        <FormItem {...formItemLayout} label=''>
                            <Radio.Group>
                                <Radio value='1'></Radio>
                                <Radio value='2'></Radio>
                            </Radio.Group>
                        </FormItem>
                        <FormItem label='' {...formItemLayout}>
                            <Select>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                        </FormItem>
                    </Form>
                </Modal>
            </div>
        );
    }
}

