import React from 'react'
import {Card,Button,Form, Select, Table, DatePicker,message} from 'antd'
import axios from './../../axios/index'
import utils from './../../utils/utils'
import Modal from 'antd/lib/modal/Modal'

const FormItem = Form.Item;
const Option = Select.Option;


export default class Order extends React.Component{
    state={
        orderInfo:{},
        orderConfirmVisable:false
    }

    params ={
        page:1
    }


    //ajaxmock
    requestList(){
        let _this = this;
        axios.ajax({
            url:'/table/order',
            data:{
                params:{
                    page:this.params.page
                }
            }
        }).then((res)=>{
            this.setState({             //reslistpagination
                list:res.result.item_list.map((item,index)=>{
                    item.key = index;
                    return item;
                }) , 
                pagination:utils.pagination(res,(current)=>{
                    _this.params.page = current;
                    _this.requestList();
                })
            })
        })
    }

    //modal
    handleShowModal=()=>{
        let item = this.state.selecedItem;
        console.log(this.state.selecedItem)
        if(!item){
            Modal.error({
                title:'',
                content:''
            })
        }else{
            axios.ajax({
                url:'/table/order/bike_info',
                data:{
                    params:1
                }
            }).then((res)=>{
                if(res.code == 0){
                    this.setState({
                        orderInfo:res.result,
                        orderConfirmVisable:true
                        
                    })
                    console.log(this.state.orderInfo)
                }
            })
        }
        
        
    }
    //onok
    handleFinishOrder=()=>{
        this.setState({
            orderConfirmVisable:false,
            selectedRowKeys:''
        })
        message.success('')
        this.requestList();
    }
    //
    openOrderDetail = ()=>{
        let item = this.state.selecedItem;
        if(!item){
            Modal.error({
                title:'',
                content:''
            })
        }else{
            window.open(`/#/common/order/detail/${item.id}`,'_blank')
        }

        
    }


    componentDidMount(){                
        this.requestList();
    }


    
    render(){
        //
        const rowSelection = {
            type:'radio',
            selectedRowKeys:this.state.selectedRowKeys,
        }



        //
        const endLayout = {
            labelCol:{
                span:6
            }
        }
                //
        const columns =[
            {
                title:'',
                dataIndex:'order_sn'
            },
            {
                title:'',
                dataIndex:'bike_sn'
            },
            {
                title:'',
                dataIndex:'user_name'
            },
            {
                title:'',
                dataIndex:'mobile'
            },
            {
                title:'',
                dataIndex:'distance'
            },
            {
                title:'',
                dataIndex:'total_time'
            },
            {
                title:'',
                dataIndex:'status'
            },
            {
                title:'',
                dataIndex:'start_time'
            },
            {
                title:'',
                dataIndex:'end_time'
            },
            {
                title:'',
                dataIndex:'total_free'
            },
            {
                title:'',
                dataIndex:'user_pay'
            },
        ]
        return(
            <div>
                <FilterForm></FilterForm>
                <Card className='card-warp'>
                    <div style={{marginBottom:15,marginRight:15}}>
                    <Button onClick={this.openOrderDetail}></Button>
                    <Button onClick={this.handleShowModal}></Button>
                    </div>
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.list}
                        pagination={this.state.pagination}
                        rowSelection={rowSelection}
                        onRow={(record,index)=>{
                            return{
                                onClick:()=>{
                                    this.setState({
                                        selectedRowKeys:[index],
                                        selecedItem:record
                                    })
                                }
                            }
                        }}
                    ></Table>
                </Card>
                <Modal
                title=''
                visible={this.state.orderConfirmVisable}
                onCancel={()=>{
                    this.setState({
                        orderConfirmVisable:false 
                    })
                }}
                onOk={this.handleFinishOrder}
                >
                    <Form>
                        <FormItem {...endLayout} label=''>{this.state.orderInfo.bike_sn}</FormItem>
                        <FormItem {...endLayout} label=''>{this.state.orderInfo.battery+'%'}</FormItem>
                        <FormItem {...endLayout} label=''>{this.state.orderInfo.start_time}</FormItem>
                        <FormItem {...endLayout} label=''>{this.state.orderInfo.location}</FormItem>
                    </Form>
                </Modal>
            </div>
        );
    }
}

//
class FilterForm extends React.Component{
    render(){
        
        return(
            <Form layout='inline' style={{marginBottom:20,marginLeft:20}} >
                <FormItem label="" name='city_id'>
                            <Select placeholder=''>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                                <Option value='3'></Option>
                            </Select>
                </FormItem>
                <FormItem label="" name='start_time'>
                            <DatePicker showTime format='YYYY-MM-DD HH:MM:SS'></DatePicker>
                </FormItem>
                
                <FormItem label='~' colon={false} name='end_time'>
                            <DatePicker showTime format='YYYY-MM-DD HH:MM:SS'></DatePicker>
                </FormItem>
                <FormItem label="" name='auth_status'>
                            <Select placeholder=''>
                                <Option value=''></Option>
                                <Option value='1'></Option>
                                <Option value='2'></Option>
                            </Select>
                </FormItem>
                <FormItem>
                    <Button type='primary' style={{marginLeft:20}} ></Button>
                    <Button></Button>
                </FormItem>
            </Form>
            
        );
    }
}