import React from 'react'
import { Card,Form, Select, DatePicker,Button, message } from 'antd'
import axios from './../../axios/index'

const FormItem = Form.Item;
const SelectItem = Select.Option;
export default class BikeMap extends React.Component{
    state={}
    map = ''        //map
    params={
        page:1
    }
    componentDidMount(){
        this.requestList();
    }

    //
    requestList=()=>{
        axios.ajax({
            url:'/table/map/bike_list',
            data:{
                params:this.params
            }
        }).then((res)=>{
            if(res.code == 0){  
                this.setState({
                    total_count:res.result.total_count,
                })
                this.renderMap(res);
            }
        });
    }

    //
    renderMap=(res)=>{
        /*  */
        let list = res.result.route_list;      //
        this.map = new window.BMap.Map('container');
        let gps1 = list[0].split(',');      //
        let startpoint = new window.BMap.Point(gps1[0],gps1[1]);
        let gps2 = list[list.length-1].split(',');      //
        let endPoint = new window.BMap.Point(gps2[0],gps2[1]);
        this.map.centerAndZoom(endPoint,11);   //,11
        this.map.addControl(new window.BMap.ScaleControl({anchor:window.BMAP_ANCHOR_TOP_RIGHT}));   //
       this.map.addControl(new window.BMap.NavigationControl({anchor:window.BMAP_ANCHOR_TOP_RIGHT}));
        
        /* icon, */
        //icon
        let startPointIcon = new window.BMap.Icon('/assets/start_point.png',new window.BMap.Size(36,42),{
            imageSize:new window.BMap.Size(36,42),
            anchor:new window.BMap.Size(18,42)  //iconicon
        })
        //markericon
        let bikeMarkerStart = new window.BMap.Marker(startpoint,{icon:startPointIcon})
        //addoverlay
        this.map.addOverlay(bikeMarkerStart);
        //icon
        let endPointIcon = new window.BMap.Icon('/assets/end_point.png',new window.BMap.Size(36,42),{
            imageSize:new window.BMap.Size(36,42),
            anchor:new window.BMap.Size(18,42)
        })
        let bikeMarkerEnd = new window.BMap.Marker(endPoint,{icon:endPointIcon})
        this.map.addOverlay(bikeMarkerEnd);

        /*  */
        let routeList = []; //
        list.forEach((item) => {
            let p = item.split(',')     //list,p
            routeList.push( new window.BMap.Point(p[0],p[1]));   //proutelist
        });
        //Polyline
        let polyLine = new window.BMap.Polyline(routeList,{         //
            strokeColor:'#1869AD',     //
            strokeWeight:3,        //
            setStrokeOpacity:1        //
        })
        this.map.addOverlay(polyLine);  //

        /*  */
        let servicePointList = [];  //
        let serviceList = res.result.service_list;     //
        serviceList.forEach((item)=>{
            servicePointList.push(new window.BMap.Point(item.lon,item.lat));
        })
        let polygon = new window.BMap.Polyline(servicePointList,{
            strokeColor:'#ef4136',
            getStrokeWeight:2,
        })
        this.map.addOverlay(polygon);

        /*  */
        let bikeList = [];      //
        let bikePoint = res.result.bike_list;
        bikePoint.forEach((item)=>{
            let p = item.split(',');
            bikeList = new window.BMap.Point(p[0],p[1]);
            //bikeicon
            let bikePointIcon = new window.BMap.Icon('/assets/bike.jpg',new window.BMap.Size(36,42),{
            imageSize:new window.BMap.Size(36,42),
            anchor:new window.BMap.Size(18,42)
            })
            let bikeMarker = new window.BMap.Marker(bikeList,{icon:bikePointIcon})
            this.map.addOverlay(bikeMarker);
        })
        
    }

    render(){
        return(
           <div>
               <BasicForm></BasicForm>
               <Card className='card-warp'>
                <div>{this.state.total_count}</div>
               <div style={{height:500}} id='container'></div>
               </Card>
           </div>         
        );
    }
}

class BasicForm extends React.Component{
    state={
        
    }
    //
    handleSubmit=(values)=>{
        // console.log(result)
        message.info(`${values.city} ${values.ordertime_start} ${values.ordertime_end} ${values.order_statue}`)
    }
    //
    handleClear=(e)=>{
        this.refs.bikeForm.resetFields();
        message.success('')
    }
    render(){
        return(
            <Card className='card-warp'>
                <Form layout='inline' onFinish={this.handleSubmit} ref='bikeForm'>
                    <FormItem
                    label=''
                    name='city'
                    style={{width:140}}
                    >
                        <Select placeholder=''>
                            <SelectItem value='0'></SelectItem>
                            <SelectItem value=''></SelectItem>
                            <SelectItem value=''></SelectItem>
                            <SelectItem value=''></SelectItem>
                        </Select>
                    </FormItem>
                    <FormItem
                    label=''
                    name='ordertime_start'
                    >
                       <DatePicker showTime format='YYYY-MM-DD HH:MM:SS'></DatePicker>
                    </FormItem>
                    <FormItem
                    label='~'
                    name='ordertime_end'
                    colon={false}
                    >
                       <DatePicker showTime format='YYYY-MM-DD HH:MM:SS'></DatePicker>
                    </FormItem>
                    <FormItem
                    label=''
                    name='order_statue'
                    >
                        <Select placeholder=''>
                            <SelectItem value=''></SelectItem>
                            <SelectItem value=''></SelectItem>
                            <SelectItem value=''></SelectItem>
                        </Select>
                    </FormItem>
                    
                        <Button type='primary' htmlType='submit'></Button>
                        <Button onClick={this.handleClear}></Button>
                    
                </Form>
            </Card>
        );
    }
}