import React from 'react' 
import {Card,Button,Modal} from 'antd'

export default class Modals extends React.Component{
    state = {
        showModal1:false,
        showModal2:false,
        showModal3:false,
        showModal4:false
    }
    //
    handleOpen1 = () =>{
        this.setState({
            showModal1:true
        })
    }
    handleOpen2 = () =>{
        this.setState({
            showModal2:true
        })
    }
    handleOpen3 = () =>{
        this.setState({
            showModal3:true
        })
    }
    handleOpen4 = () =>{
        this.setState({
            showModal4:true
        })
    }

    //
     confirm =()=>{
        Modal.confirm({
            title:'This is confirm message!',
            content:'some message ....balabalabala '
        })
    }
    info = () =>{
        Modal.info({
            title:'This is info message!',
            content:'some message ....balabalabala '
        })
    }
    success=()=>{
        Modal.success({
            title:'This is success message!',
            content:'some message ....balabalabala '
        })
    }
    error = () =>{
        Modal.error({
            title:'This is error message!',
            content:'some message ....balabalabala '
        })
    }
    warming = () =>{
        Modal.warning({
            title:'This is warning message!',
            content:'some message ....balabalabala '
        })
    }
    render(){
        return(
            <div>
                <Card title='' className = "card-warp">
                    <Button onClick={this.handleOpen1}>Open</Button>
                    <Button onClick={this.handleOpen2}></Button>
                    <Button onClick={this.handleOpen3}>20px</Button>
                    <Button onClick={this.handleOpen4}></Button>
                </Card>
                <Modal title='React' visible = {this.state.showModal1} onCancel = {()=>{
                    this.setState({
                        showModal1:false
                    })
                }}>
                    <p>zq</p>
                </Modal>

                <Modal title='' visible = {this.state.showModal2} onCancel = {()=>{
                    this.setState({
                        showModal2:false
                    })
                }} okText='' cancelText=''>
                    <p>zq</p>
                </Modal>
                
                <Modal title='20px' visible = {this.state.showModal3} onCancel = {()=>{
                    this.setState({
                        showModal3:false
                    })
                }} okText='' cancelText='' style={{top:20}}>
                    <p>zq20px</p>
                </Modal>

                <Modal title='' visible = {this.state.showModal4} onCancel = {()=>{
                    this.setState({
                        showModal4:false
                    })
                }} okText='' cancelText='' style={{top:20}} centered>
                    <p>zq</p>
                </Modal>

                <Card title='' className='card-warp'>
                    <Button onClick={this.confirm}>Confirm</Button>
                    <Button onClick={this.info}>Info</Button>
                    <Button onClick={this.success}>Success</Button>
                    <Button onClick={this.error}>Error</Button>
                    <Button onClick={this.warming}>Warming</Button>
                </Card>
            </div>
        );
    }
}