import React from 'react'
import {Button, Card,Checkbox,Form, Input,message} from 'antd'
import FormItem from 'antd/lib/form/FormItem'
import { UserOutlined, LockOutlined } from '@ant-design/icons';

export default class FormLogin extends React.Component{
    state={
        stu1:'',
        stu2:''
    }
    handleLogin=()=>{
        message.success('Login success !');
    }
    render(){
        // const {getFieldDecorator} = this.props.from;
        return(
            <div>
                <Card title='' className='card-warp'>
                    <Form layout='inline'>
                        <FormItem>
                            <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder={this.state.stu1}></Input>
                        </FormItem>
                        <FormItem>
                            <Input prefix={<LockOutlined className="site-form-item-icon" />} placeholder={this.state.stu2} type='password'></Input>
                        </FormItem>
                        <FormItem>
                            <Button type='primary' onClick={this.handleLogin}>Login</Button>
                        </FormItem>
                    </Form>
                </Card>

                <Card title='' className='card-warp'>
                    <Form initialValues={{remember:true}}>
                        <FormItem
                            name='username'
                            label=':'
                            rules={[
                                {
                                    required:true,
                                    message:'',
                                    
                                },
                                {
                                    min:5,max:10,
                                    message:'5-10'
                                }
                            ]}
                        >
                           <Input prefix={<UserOutlined className="site-form-item-icon" />} style={{width:300}} placeholder=''></Input> 
                        </FormItem>
                        <FormItem
                            name='password'
                            label=': '
                            rules={[
                                {
                                    required:true,
                                    message:'',
                                    
                                }
                            ]}
                        >
                           <Input prefix={<LockOutlined className="site-form-item-icon" />} style={{width:300}} placeholder='' type='password'></Input> 
                        </FormItem>
                        <FormItem name='remember' valuePropName='checked'>
                            <Checkbox>Remember me</Checkbox>   
                        </FormItem>
                        <FormItem>
                            <Button type='primary' htmlType='submit'>Login</Button>    
                        </FormItem>
                       
                    </Form>
                </Card>
            </div>
        );
    }
}
