import React from 'react'
import {Card} from 'antd'
import echarts from 'echarts/lib/echarts'       //
import ReactEcharts from 'echarts-for-react'    //new
import echartTheme from './../echartTheme'

import 'echarts/lib/chart/bar'          //
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/markPoint'

export default class Bar extends React.Component{

    componentWillMount(){
        echarts.registerTheme('IMOOC',echartTheme); //
    }
    
    //option    
    getOption=()=>{
        let option={
            title:{
                text:''
            },
            //
            tooltip:{
                trigger:'axis'
            },
            //x
            xAxis:{
                data:['','','','','','','']
            },
            //y,y
            yAxis:{
                type:'value'
            },
            series:[
                {
                    name:'',
                    type:'bar',
                    data:[1000,2000,1520,1200,3522,1400,5200]
                }
            ]

        }
        return option;
    }
    //option2
    getOption2=()=>{
        let option={
            title:{
                text:''
            },
            //
            legend:{
                data:['OFO','','']
            },
            //
            tooltip:{
                trigger:'axis'
            },
            //x
            xAxis:{
                data:['','','','','','','']
            },
            //y,y
            yAxis:{
                type:'value'
            },
            series:[
                {
                    name:'OFO',
                    type:'bar',
                    data:[900,2000,1520,1150,3522,1400,5200]
                },
                {
                    name:'',
                    type:'bar',
                    data:[1000,1800,1550,1200,3200,1600,3600]
                },
                {
                    name:'',
                    type:'bar',
                    data:[1100,2200,1660,1300,3100,1400,5200]
                }
            ]

        }
        return option;
    }

    render(){
        return(
            <div>
                <Card title='' className='card-warp'>
                    <ReactEcharts option={this.getOption()} theme='IMOOC'></ReactEcharts>
                </Card>
                <Card title='' className='card-warp'>
                    <ReactEcharts option={this.getOption2()} theme='IMOOC'></ReactEcharts>
                </Card>
            </div>
        );
    }
}