import React from 'react'
import {Card} from 'antd';
import echarts from 'echarts/lib/echarts'
import ReactEcharts from 'echarts-for-react'    //new
import echartTheme from './../echartTheme'  //

//
import 'echarts/lib/chart/pie'          //
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/markPoint'


export default class Pie extends React.Component{

    componentWillMount(){
        echarts.registerTheme('THEME',echartTheme); //
    }

    //getOption()
    getOption=()=>{
        let option ={
            title:{
                text:''
            },
            //
            legend:{
                orient:'vertical',  //
                right:10,
                data:['','','','','','','']
            },
            //
            series:[
                {
                    name:'',
                    type:'pie',
                    data:[
                        {
                            name:'',
                            value:1000
                        },
                        {
                            name:'',
                            value:1200
                        },
                        {
                            name:'',
                            value:1100
                        },
                        {
                            name:'',
                            value:1300
                        },
                        {
                            name:'',
                            value:1100
                        },
                        {
                            name:'',
                            value:1600
                        },
                        {
                            name:'',
                            value:1500
                        },
                    ]  
                }
            ],
            //
            tooltip:{
                // trigger:'item',
                formatter:'{a}<br/>{b} : {c} ({d}%)'
            }
        }
        return option;
    }
    getOption2=()=>{
        let option ={
            title:{
                text:''
            },
            //
            legend:{
                orient:'vertical',  //
                right:10,
                data:['','','','','','','']
            },
            //
            series:[
                {
                    name:'',
                    type:'pie',
                    radius:['40%','70%'],
                    data:[
                        {
                            name:'',
                            value:1000
                        },
                        {
                            name:'',
                            value:1200
                        },
                        {
                            name:'',
                            value:1100
                        },
                        {
                            name:'',
                            value:1300
                        },
                        {
                            name:'',
                            value:1100
                        },
                        {
                            name:'',
                            value:1600
                        },
                        {
                            name:'',
                            value:1500
                        },
                    ]  
                }
            ],
            //
            tooltip:{
                // trigger:'item',
                formatter:'{a}<br/>{b} : {c} ({d}%)'
            }
        }
        return option;
    }
    getOption3=()=>{
        let option ={
            title:{
                text:''
            },
            //
            legend:{
                orient:'vertical',  //
                right:10,
                data:['','','','','','','']
            },
            //
            series:[
                {
                    name:'',
                    type:'pie',
                    // radius:['40%','70%'],
                    data:[
                        {
                            name:'',
                            value:1000
                        },
                        {
                            name:'',
                            value:1200
                        },
                        {
                            name:'',
                            value:1100
                        },
                        {
                            name:'',
                            value:1300
                        },
                        {
                            name:'',
                            value:1100
                        },
                        {
                            name:'',
                            value:1600
                        },
                        {
                            name:'',
                            value:1500
                        },
                    ].sort((a,b)=>a.value-b.value)  
                }
            ],
            //
            tooltip:{
                // trigger:'item',
                formatter:'{a}<br/>{b} : {c} ({d}%)'
            },
            roseType:'radius'
        }
        return option;
    }

    render(){
        return(
            <div>
                <Card className='card-warp' title=''>
                    <ReactEcharts option={this.getOption()} theme='THEME'></ReactEcharts> 
                </Card>

                <Card className='card-warp' title=''>
                    <ReactEcharts option={this.getOption2()} theme='THEME'></ReactEcharts> 
                </Card>

                <Card className='card-warp' title=''>
                    <ReactEcharts option={this.getOption3()} theme='THEME'></ReactEcharts> 
                </Card>
            </div>
        );
    }
}