import React from 'react'
import {Card, Input,Radio,InputNumber,Select,Switch, DatePicker, TimePicker,Upload,message, Checkbox, Button } from 'antd'
import Form from 'antd/lib/form/Form'
import FormItem from 'antd/lib/form/FormItem'
import { UserOutlined, LockOutlined,LoadingOutlined,PlusOutlined } from '@ant-design/icons';
import moment from 'moment'
import TextArea from 'antd/lib/input/TextArea';


const {Option} = Select;
const dateFormat = 'YYYY/MM/DD';
// const { loading, imageUrl } = this.state;
//     const uploadButton = (
//       <div>
//         {loading ? <LoadingOutlined /> : <PlusOutlined />}
//         <div style={{ marginTop: 8 }}>Upload</div>
//       </div>
//     );


function getBase64(img, callback) {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
  }
export default class FormRegister extends React.Component{


    state={
        stu1:'',
        stu2:'',
        value:1,
        loading:false
    }
    //
    // handleClick=()=>{
    //     let userInfo = this.props.form.getFieldsValue();
    //     console.log(JSON.stringify(userInfo));
    // }
    //
    handleChange=(e)=>{
        this.setState({
            value:e.target.value
        });
    }
      
    //
     beforeUpload=(file)=> {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
        if (!isJpgOrPng) {
          message.error('You can only upload JPG/PNG file!');
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
          message.error('Image must smaller than 2MB!');
        }
        return isJpgOrPng && isLt2M;
      }
    //   handleChange = info => {
    //     if (info.file.status === 'uploading') {
    //       this.setState({ loading: true });
    //       return;
    //     }
    //     if (info.file.status === 'done') {
    //       // Get this url from response in real world.
    //       getBase64(info.file.originFileObj, imageUrl =>
    //         this.setState({
    //           imageUrl,
    //           loading: false,
    //         }),
    //       );
    //     }
    //   };
    uploadChange=(info)=>{
        if(info.file.status === 'uploading'){
            // this.setState({loading:true});
            return;
        }
        if(info.file.status === 'done'){
            getBase64(info.file.originFileObj, imageUrl =>
                this.setState({
                  imageUrl,
                  loading: false,
                }),
              );
        }
    }
    render(){
        const { loading, imageUrl } = this.state;
        const uploadButton = (
        <div>
            {loading ? <LoadingOutlined /> : <PlusOutlined />}
            <div style={{ marginTop: 8 }}>Upload</div>
        </div>
        );
        const formItemLayout ={
            labelCol:{
                xs:24,
                sm:4
            },
            warpperCol:{
                xs:24,
                sm:16
            }
        }
        //
        // const offsetLayout={
        //     warpperCol:{
        //         xs:24,
        //         sm:{
        //             span:12,
        //             offset:4
        //         }
        //     }
        // }
        return(
            <div>
                <Card title='' className='card-warp'>
                    <Form>
                        {/*  */}
                        <FormItem label='' 
                        name='username'
                        rules={[
                            {
                            required:'true',
                            message:''
                            },
                            {
                                min:5,max:10,
                                message:'5-10'
                            }
                        ]}
                        {...formItemLayout}
                        >
                            <Input style={{width:300}} prefix={<UserOutlined className="site-form-item-icon" />} placeholder={this.state.stu1}></Input>
                        </FormItem>
                        {/*  */}
                        <FormItem label='&nbsp;&nbsp;&nbsp;' 
                        name='password'
                        rules={[
                            {
                            required:'true',
                            message:''
                            },
                            {
                                min:5,max:10,
                                message:'5-10'
                            }
                        ]}
                        {...formItemLayout}
                        >
                            <Input type='password' style={{width:300}} prefix={<LockOutlined className="site-form-item-icon" />} placeholder={this.state.stu2}></Input>
                        </FormItem>
                        {/*  */}
                        <FormItem
                            // name='sex'
                            label='&nbsp;&nbsp;&nbsp;'
                            // initialValue='1'
                            // rules={[
                            //     {
                            //         required:true
                            //     }
                            // ]}
                            {...formItemLayout}
                        >
                            <Radio.Group value={this.state.value} onChange={this.handleChange}>
                                <Radio value={1}></Radio>
                                <Radio value={2}></Radio>
                            </Radio.Group>
                        </FormItem>
                        {/*  */}
                        <FormItem
                            name='age'
                            label='&nbsp;&nbsp;&nbsp;'
                            {...formItemLayout}
                        >
                            <InputNumber min={18} max={100} defaultValue={18}/>
                        </FormItem>
                        {/*  */}
                        <FormItem
                            name='role'
                            label=''
                            {...formItemLayout}
                        >
                            <Select defaultValue='student' style={{width:300}}>
                                <Option value='student'></Option>
                                <Option value='leader'></Option>
                                <Option value='stuff'></Option>
                                <Option value='cityzien'></Option>
                            </Select>
                        </FormItem>
                        {/*  */}
                        <FormItem
                            name='gift'
                            label=''
                            {...formItemLayout}
                        >
                            <Select
                                mode='multiple'
                                placeholder=''
                                optionLabelProp='label'
                                style={{width:300}}
                            >
                                <Option value='sing' label=''></Option>
                                <Option value='dance' label=''></Option>
                                <Option value='study' label=''></Option>
                                <Option value='playgame' label=''></Option>
                                <Option value='paint' label=''></Option>
                            </Select>
                        </FormItem>
                        {/*  */}
                        <FormItem
                            label=''
                            {...formItemLayout}
                        >
                            <Switch checkedChildren='' unCheckedChildren='' defaultunChecked></Switch>
                        </FormItem>
                        {/*  */}
                        <FormItem label='' {...formItemLayout}>
                            <DatePicker defaultValue={moment('2000/02/02',dateFormat)} format={dateFormat}></DatePicker>
                        </FormItem>
                        {/*  */}
                        <FormItem
                            label=''
                            {...formItemLayout}
                        >
                            <TextArea placeholder='' autoSize={{minRows:2,maxRows:6}} style={{width:300}}></TextArea>
                        </FormItem>
                        {/*  */}
                        <FormItem
                            label=''
                            {...formItemLayout}
                        >
                            <TimePicker ></TimePicker>
                        </FormItem>
                        {/*  */}
                        <FormItem
                            label=''
                            {...formItemLayout}
                        >
                            <Upload
                            name='uploadUser'
                            listType='picture-card'
                            showUploadList={true}
                            onChange={this.uploadChange}
                            action='https://www.mocky.io/v2/5cc8019d300000980a055e76'
                            >
                                {imageUrl ? <img src={imageUrl} alt="avatar" style={{ width: '100%' }} /> : uploadButton}
                            </Upload>
                        </FormItem>
                        {/*  */}
                        <FormItem style={{marginLeft:110}}>
                            <Checkbox ><a href='#'></a></Checkbox>
                        </FormItem>
                        <FormItem style={{marginLeft:110}}>
                            <Button onClick={this.handleClick}></Button>
                        </FormItem>
                    </Form>
                </Card>
            </div>
        );
    }
}
