import { Card } from 'antd'
import React from 'react'
import './detail.less'
import axios from './../../axios/index'
import FormItem from 'antd/lib/form/FormItem'

// const FormItem = From.item;

export default class Detail extends React.Component{
    state={}

    componentDidMount(){
        let orderId = this.props.match.params.orderId;          //id
        if(orderId){
              this.getDetailInfo(orderId);  
            //   alert(orderId)
        }
    }

    //
    getDetailInfo = (orderId)=>{
        axios.ajax({
            url:'/table/order/detail',
            data:{
                params:{
                    orderId:orderId
                }
            }
        }).then((res)=>{
            if(res.code == 0){
                this.setState({
                    orderInfo:res.result
                })
                
            }
            this.renderMap(res.result);
        })
    }

    //
   renderMap =(result)=>{
       //
      this.map = new window.BMap.Map('orderDetailMap',{enableMapClick:false})
      //
        this.addMapControl();
        
        //
        this.drawBikeRoute(result.position_list);
        //
        this.drawServiceArea(result.area);
   }
   //
   addMapControl =()=>{
       let map = this.map;
       map.addControl(new window.BMap.ScaleControl({anchor:window.BMAP_ANCHOR_TOP_RIGHT}));
       map.addControl(new window.BMap.NavigationControl({anchor:window.BMAP_ANCHOR_TOP_RIGHT}));
   }
   //bike
   drawBikeRoute = (positionList)=>{
    //    let map = this.map;
        let startPoint ='';
        let endPoint = '';
        if(positionList.length>0){
            let first = positionList[0];
            let last = positionList[positionList.length-1]
            startPoint =  new window.BMap.Point(first.lon,first.lat)        //
            //
            let startIcon = new window.BMap.Icon('/assets/start_point.png',new window.BMap.Size(36,42),{
                 imageSize:new window.BMap.Size(36,42),
                 anchor:new window.BMap.Size(36,42)
             })
            //iconmakericon
            let startMarker = new window.BMap.Marker(startPoint,{icon:startIcon});
            //marker
            this.map.addOverlay(startMarker);

            //
            endPoint =  new window.BMap.Point(last.lon,last.lat)        //
            //
            let endIcon = new window.BMap.Icon('/assets/end_point.png',new window.BMap.Size(36,42),{
                 imageSize:new window.BMap.Size(36,42),
                 anchor:new window.BMap.Size(36,42)
             })
             let endMarker = new window.BMap.Marker(endPoint,{icon:endIcon});
             //marker
            this.map.addOverlay(endMarker);

            //
            let trackPoint = [];
            for(let i=0;i<positionList.length;i++){
                let point = positionList[i];
                trackPoint.push(new window.BMap.Point(point.lon,point.lat));
                //trackpoint
            }
            let polyline = new window.BMap.Polyline(trackPoint,{
                setStrokeColor:'#1869AD',
                getStrokeWeight:2,
                getStrokeOpacity:1
            })
            this.map.addOverlay(polyline);
            //
            this.map.centerAndZoom(endPoint,11); 
    
        }   
    }   
    //
    drawServiceArea = (positionList)=>{
        let trackPoint = [];
            for(let i=0;i<positionList.length;i++){
                let point = positionList[i];
                trackPoint.push(new window.BMap.Point(point.lon,point.lat));
                //trackpoint
            }
        let polygon = new window.BMap.Polygon(trackPoint,{
                setStrokeColor:'#CE0000',
                getStrokeWeight:2,
                getStrokeOpacity:1,
                fillColor:'#ff8605',
        })
        this.map.addOverlay(polygon);
    } 
   
    render(){
        const formItemLayout = ({
            labelCol:{
                span:4
            },
            contentCol:{
                span:20
            }
        })
        const info = this.state.orderInfo || {};
        return(
            <div>
                <div id='orderDetailMap' className='order-map'></div>
                <Card className='detail-card'>
                <div className='detail-items'>
                    <div className='item-title'></div>
                    <FormItem label='' colon={false} {...formItemLayout}>{info.mode ==1?'':''}</FormItem>
                    <FormItem label='' colon={false} {...formItemLayout}>{info.order_sn}</FormItem>
                    <FormItem label='' colon={false} {...formItemLayout}>{info.user_name}</FormItem>
                    <FormItem label='' colon={false} {...formItemLayout}>{info.mobile}</FormItem>
                </div>

                <div className='detail-items'>
                    <div className='item-title'></div>
                    <FormItem label='' colon={false} {...formItemLayout}>{info.start_location}</FormItem>
                    <FormItem label='' colon={false} {...formItemLayout}>{info.end_location}</FormItem>
                    <FormItem label='' colon={false} {...formItemLayout}>{info.distance}</FormItem>
                </div>
                </Card>
            </div>  
        )
    }
}